
#operation:hover{ 
color:red
}
#btn:hover{ 
background-color:red
}
.modal-scale-transition{
  transition: transform,opacity .3s ease;
}
.modal-scale-enter,
.modal-scale-leave {
    opacity: 0;
}
.modal-scale-enter {
  transform: scale(1.1);
}
.modal-scale-leave {
  transform: scale(0.8);
}



